              >
                –î–∞—Ç–∞ —Ä–æ–∂–¥–µ–Ω–∏—è
              </pos-input>
            </form-item>
          </div>
          <div class="col-lg-7 col-md-12">
            <form-item
              :with-slotted-error="$v.passportInfo.$error"
              error="–£–∫–∞–∂–∏—Ç–µ —Å–µ—Ä–∏—é –∏ –Ω–æ–º–µ—Ä –ø–∞—Å–ø–æ—Ä—Ç–∞"
            >
              <pos-input
                v-model="passportInfo"
