                :error="$v.passportInfo.$error"
                :valid="!$v.passportInfo.$invalid"
                @blur="$v.passportInfo.$touch()"
              >
                –°–µ—Ä–∏—è –∏ –Ω–æ–º–µ—Ä –ø–∞—Å–ø–æ—Ä—Ç–∞
              </pos-input>
            </form-item>
          </div>
        </div>

        <form-item
          :with-slotted-error="$v.fmsDate.$error"
