          >
            <pos-input
              v-model="monthIncome"
              maxlength="9"
              inputmode="decimal"
              :mask="$$mask.currencyMask"
              :error="$v.monthIncome.$error"
              @blur="$v.monthIncome.$touch()"
            >
              <template v-slot:maskIcon>
                ‚ÇΩ
              </template>
              –ü–µ—Ä—Å–æ–Ω–∞–ª—å–Ω—ã–π –¥–æ—Ö–æ–¥ –≤ –º–µ—Å—è—Ü
            </pos-input>
          </form-item>
          <form-item>
            <pos-checkbox
              v-model="pension"
              bold-font
            >
              –Ø –ø–æ–ª—É—á–∞—é –ø–µ–Ω—Å–∏—é
            </pos-checkbox>
          </form-item>
        </div>

        <form-item>
          <div class="personal-data__agreement">
            <pos-checkbox
              v-model="$v.agree.$model"
              :invalid="$v.agree.$error"
            >
              <span class="personal-data__agreement-text">
                –Ø –ø—Ä–∏–Ω–∏–º–∞—é
                <div
