                  >
                    —É—Å–ª–æ–≤–∏—è –æ–±—Ä–∞–±–æ—Ç–∫–∏ –ø–µ—Ä—Å–æ–Ω–∞–ª—å–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö
                  </span>
                </div>
                –∏
                <div class="personal-data__link-text">
                  <span
                    @click="showModal('CreditParameters')"
                  >
                    –æ–∑–Ω–∞–∫–æ–º–ª–µ–Ω —Å –ø–∞—Ä–∞–º–µ—Ç—Ä–∞–º–∏ –∫—Ä–µ–¥–∏—Ç–æ–≤–∞–Ω–∏—è.
                  </span>
                </div>
