      </template>

      <template v-slot:buttons>
        <pos-button
          :disabled="$v.$error"
          @click="click"
        >
          –ö –≤—ã–±–æ—Ä—É –ø—Ä–æ–¥—É–∫—Ç–∞
        </pos-button>
      </template>
    </form-wrapper>
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields';
import { mapGetters } from 'vuex';
import { personalData } from '@/utils/validation';
import { SOCIAL_STATUSES } from '@/utils/constants';
import { getFirstLetter } from '@/utils/string';

import FormWrapper from '@/components/FormWrapper';
import PosButton from '@/components/Button';
import PosCheckbox from '@/components/Checkbox';
import PosInput from '@/components/Input';
import FormItem from '@/components/FormItem';
import Suggestions from '@/components/Suggestions';
import Tabs from '@/components/Tabs';
import Conditions from '@/documents/ProcessingConditions';
import ModalWindow from '@/components/ModalWindow';
import CreditParameters from '@/documents/CreditParameters';

const GENDER_NOT_FOUND = 'UNKNOWN';

export default {
  name: 'PersonalData',
  components: {
    CreditParameters,
    ModalWindow,
    PosInput,
    PosButton,
    FormWrapper,
    PosCheckbox,
    FormItem,
    Suggestions,
    Tabs,
    Conditions,
  },
  data() {
    return {
      agree: false,
      showGender: false,
      tabs: [
        { value: 'm', title: '–ú—É–∂—Å–∫–æ–π' },
        { value: 'f', title: '–ñ–µ–Ω—Å–∫–∏–π' },
      ],
      document: '',
    };
  },
  validations: personalData,
  computed: {
    ...mapFields({
      clientName: 'form.clientName',
      dateOfBirth: 'form.dateOfBirth',
      passportInfo: 'form.passportInfo',
      fmsDate: 'form.fmsDate',
      monthIncome: 'form.monthIncome',
      sex: 'form.sex',
      pension: 'form.pension',
      socialStatus: 'form.socialStatus',
      organizationName: 'form.organizationName',
      isExactlyPensioner: 'form.isExactlyPensioner',
      showCredits: 'modals.showCredits',
      workInsurance: 'additionalServices.workInsurance',
      showDocument: 'showDocument',
    }),
    ...mapGetters(['FIO']),
  },
  watch: {
    pension(val) {
      if (!val) {
        this.socialStatus = SOCIAL_STATUSES.WORKS;
        this.isExactlyPensioner = false;
      } else {
        this.organizationName = '';
        this.isExactlyPensioner = false;
        if (this.workInsurance) {
          this.$store.dispatch('removeService', 'workInsurance');
        }
      }
    },
  },
  methods: {
    click() {
      this.$v.$touch();
      if (this.FIO.firstName && this.$v.sex.$error) {
        this.showGender = true;
      }
      if (this.$v.$invalid) {
        return;
      }
      this.showCredits = false;
      this.$router.push('product');
    },
    showModal(name) {
      document.body.style.overflow = 'hidden';
      this.document = name;
      this.showDocument = true;
    },
    closeModal() {
      document.body.style.overflow = '';
      this.showDocument = false;
    },
    setGender(suggestion) {
      /**
       * –ü—Ä–æ—Å—Ç–∞–≤–ª—è–µ–º –ø–æ–ª –∫–ª–∏–µ–Ω—Ç–∞.
       * –ï—Å–ª–∏ –µ—Å—Ç—å –≤–∞—Ä–∏–∞–Ω—Ç
       * –ø—Ä–æ–≤–µ—Ä—è–µ–º –Ω–∞–ª–∏—á–∏–µ –ø–æ–ª–∞ –∏ –≤—ã—Å—Ç–∞–≤–ª—è–µ–º –µ–≥–æ
       * –∏–Ω–∞—á–µ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –ø–æ–ª–µ –≤—ã–±–æ—Ä–∞ –ø–æ–ª–∞.
       * –ï—Å–ª–∏ –≤–∞—Ä–∏–∞–Ω—Ç–æ–≤ –Ω–µ—Ç—É
       * –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –ø–æ–ª–µ –≤—ã–±–æ—Ä–∞ –ø–æ–ª–∞.
       */
      if (suggestion && suggestion.data) {
        const { gender } = suggestion.data;
        if (gender !== GENDER_NOT_FOUND) {
          this.sex = getFirstLetter(gender).toLowerCase();
        } else {
          this.showGenderField();
        }
      } else {
        this.showGenderField();
      }
    },
    onFIOBlur(suggestion) {
      this.$v.clientName.$touch();
      this.setGender(suggestion);
    },
    showGenderField() {
      if (this.FIO.firstName) {
        this.showGender = true;
        this.$v.sex.$touch();
      }
    },
  },
};
</script>

