import { mapFields } from 'vuex-map-fields';
import { insuranceCalculator, sumPerMonthsSimple } from '@/utils/calculator';
import { INSURANCES } from '@/utils/constants';
import { getFullAge } from '@/utils/date';
import Service from '@/utils/service';

const MULTI_DEFENCE_PRICE = 1500;
const MULTI_SERVICE_PRICE = 3000;
const SMS_SERVICE_PRICE = 99;

const SERVICE_TEXTS = {
  LIFE_INSURANCE: {
    description: '–ü—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –∑–∞—ë–º—â–∏–∫–æ–º –Ω–µ—Ä–∞–±–æ—á–µ–π –≥—Ä—É–ø–ø—ã –∏–Ω–≤–∞–ª–∏–¥–Ω–æ—Å—Ç–∏ –∏–ª–∏ —É—Ö–æ–¥–µ –∏–∑ –∂–∏–∑–Ω–∏, –°—Ç—Ä–∞—Ö–æ–≤–∞—è –∫–æ–º–ø–∞–Ω–∏—è –ø–µ—Ä–µ—á–∏—Å–ª—è–µ—Ç –≤—ã–ø–ª–∞—Ç—É –≤ —Ä–∞–∑–º–µ—Ä–µ —Å—É–º–º—ã –∫—Ä–µ–¥–∏—Ç–∞ –Ω–∞ —É–∫–∞–∑–∞–Ω–Ω—ã–π –∫–ª–∏–µ–Ω—Ç–æ–º —Å—á—ë—Ç.',
    text: '–ü–µ—Ä–µ–¥–∞–π—Ç–µ —Ä–∏—Å–∫–∏ <br> –°—Ç—Ä–∞—Ö–æ–≤–æ–π –∫–æ–º–ø–∞–Ω–∏–∏ –∑–∞',
  },
  WORK_INSURANCE: {
    description: '–ü—Ä–∏ –ø–æ—Ç–µ—Ä–µ –∑–∞—ë–º—â–∏–∫–æ–º —Ä–∞–±–æ—Ç—ã, –°—Ç—Ä–∞—Ö–æ–≤–∞—è –∫–æ–º–ø–∞–Ω–∏—è –æ–ø–ª–∞—á–∏–≤–∞–µ—Ç –¥–æ 8 –µ–∂–µ–º–µ—Å—è—á–Ω—ã—Ö –ø–ª–∞—Ç–µ–∂–µ–π, –µ—Å–ª–∏ –Ω–µ —É–¥–∞—ë—Ç—Å—è –Ω–∞–π—Ç–∏ —Ä–∞–±–æ—Ç—É.',
    text: '–ü–µ—Ä–µ–¥–∞–π—Ç–µ —Ä–∏—Å–∫–∏ <br> –°—Ç—Ä–∞—Ö–æ–≤–æ–π –∫–æ–º–ø–∞–Ω–∏–∏ –∑–∞',
  },
  MULTI_DEFENCE: {
    description: '–í—ã–ø–ª–∞—Ç—ã –¥–æ 150 000 ‚ÇΩ –ø—Ä–∏ –ø–æ–≤—Ä–µ–∂–¥–µ–Ω–∏–∏ / —É—Ç—Ä–∞—Ç–µ –∏–º—É—â–µ—Å—Ç–≤–∞ –ø–æ –≤–∏–Ω–µ —Ç—Ä–µ—Ç—å–∏—Ö –ª–∏—Ü. –í—ã–ø–ª–∞—Ç—ã –ø—Ä–∏ ‚Äú–∑–∞–ª–∏–≤–µ‚Äù –∫–ª–∏–µ–Ω—Ç–æ–º —Å–æ—Å–µ–¥–µ–π. –í—ã–ø–ª–∞—Ç—ã –ø—Ä–∏ –∫—Ä–∞–∂–∞—Ö —Å –∫–∞—Ä—Ç –û–¢–ü. –í–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å –∫–æ–Ω—Å—É–ª—å—Ç–∞—Ü–∏–∏ —Å —é—Ä–∏—Å—Ç–æ–º.',
    text: '–ü–µ—Ä–µ–¥–∞–π—Ç–µ —Ä–∏—Å–∫–∏ <br> –°—Ç—Ä–∞—Ö–æ–≤–æ–π –∫–æ–º–ø–∞–Ω–∏–∏ –∑–∞',
  },
  MULTI_SERVICE: {
    description: '–ö–æ–Ω—Å—É–ª—å—Ç–∞—Ü–∏—è –ø–æ –≤–∞–∂–Ω—ã–º –≤–æ–ø—Ä–æ—Å–∞–º. <br> –Æ—Ä–∏—Å—Ç—ã –ø–æ–º–æ–≥—É—Ç —Ä–∞–∑–æ–±—Ä–∞—Ç—å—Å—è/—Å–æ—Å—Ç–∞–≤–∏—Ç—å —é—Ä–∏–¥–∏—á–µ—Å–∫–∏–µ –¥–æ–∫—É–º–µ–Ω—Ç—ã, —Ç–∞–∫–∂–µ –ø—Ä–æ–∫–æ–Ω—Å—É–ª—å—Ç–∏—Ä—É—é—Ç –ø–æ –Ω–∞–ª–æ–≥–æ–≤—ã–º –≤–æ–ø—Ä–æ—Å–∞–º –∏ –ø—Ä–∞–≤–∞–º, –µ—Å–ª–∏ –≤–∞—Å —Ö–æ—Ç—è—Ç –æ—à—Ç—Ä–∞—Ñ–æ–≤–∞—Ç—å.',
    text: '–ü–µ—Ä–µ–¥–∞–π—Ç–µ —Ä–∏—Å–∫–∏ <br> –°—Ç—Ä–∞—Ö–æ–≤–æ–π –∫–æ–º–ø–∞–Ω–∏–∏ –∑–∞',
  },
  SMS_SERVICE: {
    description: '–ö–æ—Ä–æ—Ç–∫–∏–µ —Å–º—Å-–æ—Ç—á—ë—Ç—ã –ø—Ä–∏ –∑–∞—á–∏—Å–ª–µ–Ω–∏–∏ —Å—Ä–µ–¥—Å—Ç–≤: —Å—É–º–º–∞ –∑–∞—á–∏—Å–ª–µ–Ω–∏—è, –æ—Å—Ç–∞–≤—à–∞—è—Å—è —Å—É–º–º–∞ –∑–∞–¥–æ–ª–∂–µ–Ω–Ω–æ—Å—Ç–∏ –∏ –¥—Ä—É–≥–æ–µ.',
    text: '–ë—É–¥—å—Ç–µ –≤ –∫—É—Ä—Å–µ, –¥–∞–∂–µ –ø—Ä–∏ <br> –æ—Ç—Å—É—Ç—Å—Ç–≤–∏–∏ –ò–Ω—Ç–µ—Ä–Ω–µ—Ç–∞ –∑–∞',
  },
};

export default {
  name: 'ServiceListProvider',
  computed: {
    ...mapFields([
      'product',
      'totalSum',
      'form.dateOfBirth',
      'form.sex',
      'form.pension',
      'selectedServices',
    ]),

    services() {
      const availableServices = [];
      /* –§–æ—Ä–º–∏—Ä—É–µ–º —Å—Ç—Ä–∞—Ö–æ–≤–∫–∏ –∏ –∫–æ—Ä–æ–±–æ—á–Ω—ã–µ —É—Å–ª—É–≥–∏ */
      const lifeInsurance = {
        title: '–°—Ç—Ä–∞—Ö–æ–≤–∞–Ω–∏–µ –∂–∏–∑–Ω–∏ –∏ <br> –∑–¥–æ—Ä–æ–≤—å—è',
        name: 'lifeInsurance',
        price: this.calcInsurance(INSURANCES.LIFE),
        pricePerMonth: this.calcInsuranceSumPerMonth(INSURANCES.LIFE),
        description: SERVICE_TEXTS.LIFE_INSURANCE.description,
        text: SERVICE_TEXTS.LIFE_INSURANCE.text,
      };
      const workInsurance = {
        title: 'C—Ç—Ä–∞—Ö–æ–≤–∞–Ω–∏–µ —Ñ–∏–Ω–∞–Ω—Å–æ–≤—ã—Ö —Ä–∏—Å–∫–æ–≤',
        name: 'workInsurance',
        price: this.calcInsurance(INSURANCES.FINANCE),
        pricePerMonth: this.calcInsuranceSumPerMonth(INSURANCES.FINANCE),
        description: SERVICE_TEXTS.WORK_INSURANCE.description,
        text: SERVICE_TEXTS.WORK_INSURANCE.text,
      };
      const multiDefence = {
        title: '–ú—É–ª—å—Ç–∏–∑–∞—â–∏—Ç–∞',
        name: 'multiDefence',
        price: MULTI_DEFENCE_PRICE,
        pricePerMonth: this.calcServiceSumPerMonth(MULTI_DEFENCE_PRICE),
        description: SERVICE_TEXTS.MULTI_DEFENCE.description,
        text: SERVICE_TEXTS.MULTI_DEFENCE.text,
      };
      const multiService = {
        title: '–ú—É–ª—å—Ç–∏—Å–µ—Ä–≤–∏—Å',
        name: 'multiService',
        price: MULTI_SERVICE_PRICE,
        pricePerMonth: this.calcServiceSumPerMonth(MULTI_SERVICE_PRICE),
        description: SERVICE_TEXTS.MULTI_SERVICE.description,
        text: SERVICE_TEXTS.MULTI_SERVICE.text,
      };
      const smsService = {
        title: '–°–ú–°-–ø–∞–∫–µ—Ç',
        name: 'smsService',
        price: this.calcSMSPrice(SMS_SERVICE_PRICE),
        pricePerMonth: SMS_SERVICE_PRICE,
        description: SERVICE_TEXTS.SMS_SERVICE.description,
        text: SERVICE_TEXTS.SMS_SERVICE.text,
      };
      /* –ü—Ä–æ–≤–µ—Ä—è–µ–º –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç—å —Å—Ç—Ä–∞—Ö–æ–≤–∫–∏ –∂–∏–∑–Ω–∏ */
      if (Service.availableLifeInsurance(this.clientAge)) {
        availableServices.push(lifeInsurance);
      }
      /* –ü—Ä–æ–≤–µ—Ä—è–µ–º –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç—å —Å—Ç—Ä–∞—Ö–æ–≤–∫–∏ —Ä–∞–±–æ—Ç—ã */
      if (Service.availableWorkInsurance(this.clientAge, this.sex, this.pension)) {
        availableServices.push(workInsurance);
      }
      /* –ü—Ä–æ–≤–µ—Ä—è–µ–º –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç—å –∫–æ—Ä–æ–±–æ—á–Ω—ã—Ö —É—Å–ª—É–≥ */
      if (Service.availableBoxInsurance(this.clientAge)) {
        availableServices.push(multiDefence);
        availableServices.push(multiService);
        availableServices.push(smsService);
      }

      return availableServices;
    },
    clientAge() {
      return getFullAge(this.dateOfBirth);
    },
    productDuration() {
      return this.product.duration;
    },
  },
  methods: {
    calcServiceSumPerMonth(price) {
      return sumPerMonthsSimple(price, this.productDuration);
    },
    calcInsuranceSumPerMonth(insuranceType = '') {
      const insurance = this.calcInsurance(insuranceType);
      return sumPerMonthsSimple(insurance, this.productDuration);
    },
    calcInsurance(insuranceType) {
      return insuranceCalculator(this.totalSum, this.productDuration, insuranceType);
    },
    calcSMSPrice(price) {
      return price * this.productDuration;
    },
  },
  render() {
    return this.$scopedSlots.default({
      services: this.services,
      selectedServices: this.selectedServices,
    });
  },
};
