/**
 * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç —á–∏—Å–ª–æ –≤ —É–¥–æ–±–æ—á–∏—Ç–∞–µ–º–æ–º —Ñ–æ—Ä–º–∞—Ç–µ
 * @example 1000 - 1 000, 120000 - 120 000
 * @param num
 * @returns {string}
 */
export const numberToCurrency = num => new Intl.NumberFormat('ru-RU').format(num);

/**
 * –û—á–∏—â–∞–µ—Ç —Å—Ç—Ä–æ–∫—É –æ—Ç –ª—é–±—ã—Ö —Å–∏–º–≤–æ–ª–æ–≤, –∫—Ä–æ–º–µ —á–∏—Å–µ–ª, –ª–∏–±–æ 0 –µ—Å–ª–∏ —Å—Ç—Ä–æ–∫–∞ –ø—É—Å—Ç–∞—è
 * @example 1 000 - 1000, 120-000 - 120000
 * @param {string} str
 * @returns {number}
 */
export const currencyToNumber = str => +(str.replace(/[^0-9,]/g, '').replace(/,/g, '.'));

/**
 * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç —á–∏—Å–ª–æ –æ–∫—Ä—É–≥–ª–µ–Ω–Ω–æ–µ –¥–æ –±–ª–∏–∂–∞–π—à–µ–≥–æ —Ü–µ–ª–æ–≥–æ –¥–µ—Å—è—Ç–∏—á–Ω–æ–≥–æ —á–∏—Å–ª–∞
 * @example 32766 - 32770, 24003 - 24000
 * @param num
 * @returns {number}
 */
export const roundTo10 = num => Math.round(num / 10) * 10;
