<template>
  <div class="product-page">
    <form-wrapper>
      <template v-slot:info>
        –≠—Ç–æ –Ω–∞—à–∏ –∫—Ä–µ–¥–∏—Ç–Ω—ã–µ –ø—Ä–æ–¥—É–∫—Ç—ã –∏ —É—Å–ª—É–≥–∏. –†–∞—Å—á—ë—Ç –ø—Ä–µ–¥–≤–∞—Ä–∏—Ç–µ–ª—å–Ω—ã–π. <br>
        –û–∫–æ–Ω—á–∞—Ç–µ–ª—å–Ω—ã–µ —É—Å–ª–æ–≤–∏—è –æ–∑–≤—É—á–∏—Ç —Å–æ—Ç—Ä—É–¥–Ω–∏–∫ –ë–∞–Ω–∫–∞.
      </template>

      <template v-slot:title>
        <total-sum v-if="$mq !== 'desktop'" />
        <template>
          –í—ã–±–µ—Ä–∏—Ç–µ –ø—Ä–æ–¥—É–∫—Ç
        </template>
      </template>

      <template v-slot:default>
        <products
          :products="products"
          @show-first-payment="firstPaymentClick"
          @select="clearFieldsAndHide"
        />

        <div
          v-if="showFirstPayment"
          class="product-page__first-payment"
        >
          <form-item
            with-slotted-description
            no-margin-bottom
          >
            <pos-input
              ref="input"
              v-model="firstPaymentVModel"
              title="–ü–µ—Ä–≤–æ–Ω–∞—á–∞–ª—å–Ω—ã–π –≤–∑–Ω–æ—Å"
              inputmode="decimal"
              :mask="$$mask.currencyMask"
              @blur="hideFirstPayment"
            >
              <template v-slot:maskIcon>
                ‚ÇΩ
              </template>
            </pos-input>
            <template #description>
              –ú–∏–Ω–∏–º–∞–ª—å–Ω–∞—è —Å—É–º–º–∞ –∫—Ä–µ–¥–∏—Ç–∞ 2000 —Ä—É–±.<br>
              –ü–µ—Ä–≤—ã–π –≤–∑–Ω–æ—Å –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –±–æ–ª—å—à–µ —á–µ–º 90% –æ—Ç —Å—Ç–æ–∏–º–æ—Å—Ç–∏ —Ç–æ–≤–∞—Ä–∞.
            </template>
          </form-item>

          <pos-button
            :disabled="!availableFirstPayment"
            @click="accept"
          >
            –ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç—å
          </pos-button>
        </div>

        <service-list-provider v-slot="{ services, selectedServices }">
          <services
            v-if="services.length"
            :services="services"
            :selected-services="selectedServices"
          />
        </service-list-provider>
      </template>

      <template v-slot:buttons>
        <pos-button @click="click">
          –î–∞–ª–µ–µ
        </pos-button>
      </template>
    </form-wrapper>
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields';
import { sumPerMonth } from '@/utils/calculator';
import { CREDIT_DURATIONS } from '@/utils/constants';
import { currencyToNumber } from '@/utils/number';
import { creditProductsOne, creditProductsTwo } from '@/utils/creditProducts';

import FormWrapper from '@/components/FormWrapper';
import PosButton from '@/components/Button';
import Products from './components/Products';
import Services from './components/Services';
import PosInput from '@/components/Input';
import TotalSum from '@/components/TotalSum';
import FormItem from '@/components/FormItem';

export default {
  name: 'ProductPage',
  components: {
    FormWrapper,
    PosButton,
    Products,
    Services,
    PosInput,
    TotalSum,
    FormItem,
  },
  data() {
    return {
      agree: false,
      showServices: false,
      showFirstPayment: false,
      firstPaymentVModel: '',
    };
  },
  computed: {
    ...mapFields({
      currentProduct: 'product',
      totalSum: 'totalSum',
      formType: 'formType',
      firstPayment: 'firstPayment',
      displayProducts: 'displayProducts',
    }),
    localTotalSum() {
      const { servicesSum } = this.$store.getters;
      return this.totalSum + servicesSum;
    },
    monthPayment() {
      return {
        sixMonth: sumPerMonth(this.localTotalSum, CREDIT_DURATIONS.SIX_MONTHS),
        twelveMonthCredit: this.computedTwelveMonthValue,
        twelveMonthInstallment: sumPerMonth(this.localTotalSum, CREDIT_DURATIONS.TWELVE_MONTHS),
        eighteenMonth: this.computedEighteenMonthValue,
        twoYears: this.computedTwoYearsValue,
      };
    },
    products() {
      return this.displayProducts === 'firstVariant'
        ? creditProductsOne(this.monthPayment)
        : creditProductsTwo(this.monthPayment);
    },
    computedTwelveMonthValue() {
      const sum = this.localTotalSum - this.firstPayment;
      return sumPerMonth(sum, CREDIT_DURATIONS.TWELVE_MONTHS);
    },
    computedEighteenMonthValue() {
      const sum = this.localTotalSum - this.firstPayment;
      return sumPerMonth(sum, CREDIT_DURATIONS.EIGHTEEN_MONTH);
    },
    computedTwoYearsValue() {
      const sum = this.localTotalSum - this.firstPayment;
      return sumPerMonth(sum, CREDIT_DURATIONS.TWO_YEARS);
    },

    availableFirstPayment() {
      const MIN_SUM = 2000;
      // –º–∞–∫—Å–∏–º–∞–ª—å–Ω—ã–π –ø–ª–∞—Ç–µ–∂ –Ω–µ –±–æ–ª–µ–µ 90% –æ—Ç —Å—Ç–æ–∏–º–æ—Å—Ç–∏ —Ç–æ–≤–∞—Ä–∞
      const MAX_SUM = Math.ceil(this.totalSum * 0.9);
      const firstPayment = currencyToNumber(this.firstPaymentVModel);
      if (!firstPayment) return true;

      return firstPayment >= MIN_SUM && firstPayment <= MAX_SUM;
    },
  },
  created() {
    // eslint-disable-next-line prefer-destructuring
    this.currentProduct = this.products[0];
  },
  methods: {
    click() {
      return this.formType === 'short'
        ? this.$router.push('contacts')
        : this.$router.push('passport');
    },
    firstPaymentClick() {
      this.showFirstPayment = true;
      this.$nextTick(() => {
        this.$refs.input.$refs.input.focus();
      });
    },
    accept() {
      this.firstPayment = currencyToNumber(this.firstPaymentVModel);
      this.clearFieldsAndHide();
    },
    hideFirstPayment() {
      const hasVal = !!this.firstPaymentVModel;
      if (!hasVal) {
        this.showFirstPayment = false;
      }
    },
    clearFieldsAndHide() {
      this.firstPaymentVModel = '';
      this.showFirstPayment = false;
    },
  },
};
</script>

