import { post } from '@/utils/http';

const API = process.env.VUE_APP_DADATA_API;
const TOKEN = process.env.VUE_APP_DADATA_KEY;

const concatUrl = query => `${API}${query}`;

/**
 * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –≤–∞—Ä–∏–∞–Ω—Ç—ã –ø–æ–¥—Å–∫–∞–∑–æ–∫
 * @param {string} type - —Ç–∏–ø –ø–æ–¥—Å–∫–∞–∑–∫–∏ (—Ä–∞–∑–¥–µ–ª "–í–æ–∑–º–æ–∂–Ω–æ—Å—Ç–∏")
 * @param {Object} data - –ø–∞—Ä–∞–º–µ—Ç—Ä—ã –∑–∞–ø—Ä–æ—Å–∞
 * @returns {Promise<*>}
 */
const getSuggestions = (type, data) => {
  const url = concatUrl(type);
  const headers = {
    'Content-Type': 'application/json',
    Accept: 'application/json',
    Authorization: `Token ${TOKEN}`,
  };
  const params = {
    headers,
  };
  return post(
    url,
    data,
    params,
  )
    .then(result => result);
};

export default getSuggestions;

