import Vue from 'vue';
import Router from 'vue-router';

import { ROUTE_IDS } from '@/utils/constants';

// –®–∞–≥–∏ —Å–º–∞—Ä—Ç –∞–Ω–∫–µ—Ç—ã –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è '–ø–∏–ª–æ—Ç–∞–∂'
import ProductPage from '@/modules/ProductPage';
import PersonalData from '@/modules/PersonalData';
import ContactInfo from '@/modules/ContactInfo';
import PassportData from '@/modules/PassportData';

// –ü–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏–µ –ª–∏–±–æ –æ—Ç–º–µ–Ω–∞
import SuccessPage from '@/modules/SuccessPage';
import CancelPage from '@/modules/CancelPage';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'personal',
      component: PersonalData,
      meta: {
        id: ROUTE_IDS.PERSONAL_DATA,
      },
    },
    {
      path: '/product',
      name: 'product',
      component: ProductPage,
      meta: {
        id: ROUTE_IDS.PRODUCT_PAGE,
      },
    },
    {
      path: '/contacts',
      name: 'contacts',
      component: ContactInfo,
      meta: {
        id: ROUTE_IDS.CONTACT_INFO,
      },
    },
    {
      path: '/passport',
      name: 'passport',
      component: PassportData,
      meta: {
        id: ROUTE_IDS.PASSPORT_DATA,
      },
    },
    {
      path: '/success',
      name: 'success',
      component: SuccessPage,
      meta: {
        id: ROUTE_IDS.SUCCESS_PAGE,
      },
    },
    {
      path: '/cancel',
      name: 'cancel',
      component: CancelPage,
      meta: {
        id: ROUTE_IDS.CANCEL_PAGE,
      },
    },
  ],
});
