  <section class="contact-info">
    <form-wrapper>
      <template v-slot:info>
        –ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø—Ä–µ–¥—Å—Ç–∞–≤—å—Ç–µ—Å—å. –ß–µ–º –±–æ–ª—å—à–µ –≤—ã —Ä–∞—Å—Å–∫–∞–∂–µ—Ç–µ –æ —Å–µ–±–µ, —Ç–µ–º –≤—ã—à–µ —à–∞–Ω—Å—ã –Ω–∞ <br>
        –ø–æ–ª—É—á–µ–Ω–∏–µ –∫—Ä–µ–¥–∏—Ç–∞. –ü–æ—Å–ª–µ –æ—Ç–ø—Ä–∞–≤–∫–∏ –∑–∞—è–≤–∫–∏ –≤–∞–º –ø–æ–∑–≤–æ–Ω–∏—Ç —Å–æ—Ç—Ä—É–¥–Ω–∏–∫ –ë–∞–Ω–∫–∞.
      </template>

      <template v-slot:title>
        –ö–æ–Ω—Ç–∞–∫—Ç–Ω–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è
      </template>

      <template v-slot:default>
        <div class="row">
          <div class="col-lg-5 col-md-12">
            <form-item
              description="–û—Ç–ø—Ä–∞–≤–∏–º –°–ú–° —Å –∫–æ–¥–æ–º –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è"
              :with-slotted-error="$v.phoneContacts.$error"
              :error="$$error.phoneNumberText(phoneContacts)"
            >
              <pos-input
                v-model="phoneContacts"
                :pattern="$$pattern.phonePattern"
                autocomplete="off"
                type="tel"
                placeholder="(921) 123-45-67"
                prefix="+7"
                inputmode="tel"
                :mask="$$mask.phone"
                :error="$v.phoneContacts.$error"
                :valid="!$v.phoneContacts.$invalid"
                @blur="$v.phoneContacts.$touch()"
              >
                –ú–æ–±–∏–ª—å–Ω—ã–π —Ç–µ–ª–µ—Ñ–æ–Ω
              </pos-input>
            </form-item>
          </div>
          <div class="col-lg-7 col-md-12">
            <form-item
              description="–ü—Ä–∏—à–ª–µ–º –ø–∏—Å—å–º–æ —Å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–µ–π –ø–æ –≤–∞—à–µ–π –∑–∞—è–≤–∫–µ"
              :with-slotted-error="$v.clientEmail.$error"
              error="–í–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π –∞–¥—Ä–µ—Å —ç–ª. –ø–æ—á—Ç—ã."
            >
              <pos-input
                v-model="clientEmail"
                :mask="$$mask.emailMask"
                placeholder="ivan@domain.ru"
                :error="$v.clientEmail.$error"
                :valid="!$v.clientEmail.$invalid"
                @blur="$v.clientEmail.$touch()"
              >
                –≠–ª–µ–∫—Ç—Ä–æ–Ω–Ω–∞—è –ø–æ—á—Ç–∞
              </pos-input>
