        <form-item
          description="–ù–∞–ø—Ä–∏–º–µ—Ä, –¥–µ–≤–∏—á—å—è —Ñ–∞–º–∏–ª–∏—è –≤–∞—à–µ–π –º–∞—Ç–µ—Ä–∏"
          :with-slotted-error="$v.codeWord.$error"
          error="–î–∞–ª–µ–µ –ø–æ—Ç—Ä–µ–±—É–µ—Ç—Å—è –¥–ª—è –¥–æ—Å—Ç—É–ø–∞ –∫ —É–ø—Ä–∞–≤–ª–µ–Ω–∏—é —É—Å–ª—É–≥–∞–º–∏"
        >
          <pos-input
            v-model="codeWord"
            translate
            :mask="$$mask.codeWordMask"
            :error="$v.codeWord.$error"
            :valid="!$v.codeWord.$invalid"
            @blur="$v.codeWord.$touch()"
          >
            –ü—Ä–∏–¥—É–º–∞–π—Ç–µ –∫–æ–¥–æ–≤–æ–µ —Å–ª–æ–≤–æ
          </pos-input>
        </form-item>
      </template>

      <template v-slot:buttons>
        <pos-button
          :disabled="$v.$error"
          :is-loading="isLoading"
          @click="click"
        >
          –î–∞–ª–µ–µ
        </pos-button>
      </template>
    </form-wrapper>
  </section>
</template>

<script>

import { mapActions } from 'vuex';
import { mapFields } from 'vuex-map-fields';
import { contactInfo } from '@/utils/validation';

import FormWrapper from '@/components/FormWrapper';
import PosInput from '@/components/Input';
import PosButton from '@/components/Button';
import FormItem from '@/components/FormItem';

export default {
  name: 'ContactInfo',
  components: {
    FormWrapper,
    PosInput,
    PosButton,
    FormItem,
  },
  validations: contactInfo,
  computed: {
    ...mapFields({
      clientEmail: 'form.clientEmail',
      codeWord: 'form.codeWord',
      scoring: 'form.scoring',
      phoneContacts: 'form.phoneContacts',
      isLoading: 'isLoading',
    }),
  },
  watch: {
    phoneContacts(val) {
      if (val) {
        this.$store.commit('setFullMobilePhone', { name: 'clientMobilePhone', value: val });
      }
    },
  },
  methods: {
    ...mapActions(['sendShortLoan']),

    click() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.isLoading = true;
      this.sendShortLoan().then(() => {
        this.isLoading = false;
        this.$router.push('success');
      });
    },
  },
};
</script>

