const additionalServices = () => ({
  lifeInsurance: false,
  workInsurance: false,
  multiDefence: false,
  multiService: false,
  smsService: true,
});

export default new Vuex.Store({
  state: {
    accessID: 3399,
    tradeID: 12155,
    orderID: '',
    phone: '',
    formType: 'short',
    form: defaultClientData(),
    firstPayment: 0,
    totalSum: 200000,
    step: 1,
    defaultProducts: [],
    product: null,
    additionalServices: additionalServices(),
    selectedServices: [],
    status: '',
    session: '',
    modals: {
      showCredits: true,
      showStepperModal: true,
    },
    isLoading: false,
    manualAddress: false,
    showDocument: false,

    // –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –ø–æ–ª–Ω—ã–π –∞–¥—Ä–µ—Å
    completeAddress: false,

    // –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è —Å—Ü–µ–Ω–∞—Ä–∏–µ–≤ —Å –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ–º –∫–æ–Ω–∫—Ä–µ—Ç–Ω—ã—Ö –ø—Ä–æ–¥—É–∫—Ç–æ–≤ –¥–ª—è –ø–∞—Ä—Ç–Ω–µ—Ä–∞
    displayProducts: '',
  },
  getters: {
    /**
     * –ü–æ—Å—á–∏—Ç–∞—Ç—å —Å—É–º–º—É –≤—Å–µ—Ö –≤—ã–±—Ä–∞–Ω–Ω—ã—Ö –¥–æ–ø. —É—Å–ª—É–≥
     * @param state
     * @returns {number}
     */
    servicesSum: state => state.selectedServices
      .reduce((sum, service) => sum + service.price, 0),
    /* –§–∞–º–∏–ª–∏—è –ò–º—è –û—Ç—á–µ—Å—Ç–≤–æ */
    FIO(state) {
      const [lastName, firstName, middleName] = state.form.clientName.trim().split(' ');
      return { middleName, firstName, lastName };
    },

    getField,
  },
  mutations: {
    /**
     * –ó–∞–ø—É—Å—Ç–∏—Ç—å –º—É—Ç–∞—Ü–∏—é, –∫–æ–≥–¥–∞ –Ω—É–∂–Ω–æ —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∏–Ω–∏—Ç –∑–Ω–∞—á–µ–Ω–∏—è –≤ state.form
     * @param state
     * @param data
     */
    setInitFormValues(state, data = {}) {
      Object.assign(state.form, data);
    },
    /**
     * –û–±–Ω–æ–≤–∏—Ç—å –Ω–µ—Å–∫–æ–ª—å–∫–æ –ø–æ–ª–µ–π –≤ state.form
     * –ü—Ä–∏–º–µ—Ä: { clientEmail: 'test@test.ru', clientName: '–ò–≤–∞–Ω–æ –ò–≤–∞–Ω' }
     * @param state
     * @param data
     */
    updateFormValues(state, data) {
      Object.keys(data).forEach((key) => {
        state.form[key] = data[key];
      });
    },
    /**
     * –û–±–Ω–æ–≤–∏—Ç—å –ø–æ–ª–µ –≤ state.form
     * –ü—Ä–∏–º–µ—Ä: { name: clientEmail, value: 'test@test.ru' }
     * @param state
     * @param name
     * @param value
     */
    updateFormField(state, { name, value }) {
      state.form[name] = value;
    },
    setState(state, { name, value }) {
      state[name] = value;
    },
    /**
     * –î–æ–±–∞–≤–∏—Ç—å –¥–æ–ø. —É—Å–ª—É–≥—É
     * –ü—Ä–∏–º–µ—Ä: { name: workInsurance, value: true }
     * @param state
     * @param name
     * @param value
     */
    setService(state, { name, value }) {
      state.additionalServices[name] = value;
    },
    /**
     * –î–æ–±–∞–≤–∏—Ç—å –∏–ª–∏ —É–¥–∞–ª–∏—Ç—å –≤—ã–±—Ä–∞–Ω–Ω—É—é –¥–æ–ø. —É—Å–ª—É–≥—É
     * @param state
     * @param {boolean} checked
     * @param {Object} service
     */
    setServices(state, { checked, service }) {
      if (checked) {
        state.selectedServices.push(service);
      } else {
        const index = state.selectedServices
          .findIndex(item => item.name === service.name);
        state.selectedServices.splice(index, 1);
      }
    },
    setFullMobilePhone(state, { name, value }) {
      const countryCode = '+7 ';
      state.form[name] = countryCode + value;
    },
    updateField,
  },
  actions: {
    setService({ commit }, payload = {}) {
      const { checked, service } = payload;
      commit('setService', { name: service.name, value: checked });
      commit('setServices', { checked, service });
    },
    removeService({ commit }, serviceName = '') {
      commit('setServices', { checked: false, service: { name: serviceName } });
      commit('setService', { name: serviceName, value: false });
    },
    async startPosCreditSession({ commit, state }) {
      try {
        const { accessID, tradeID, orderID } = state;
        const postData = {
          accessID,
          tradeID,
          orderID,
          phone: '',
        };
        const { data } = await posApi.startSession(postData);
        commit('setState', {
          name: 'session',
          value: data.session,
        });
      } catch (e) {
        console.log(e);
      }
    },

    async fetchPosCreditProducts({ commit, state }) {
      try {
        const { accessID, tradeID } = state;
        const { data } = await posApi.fetchProducts({ accessID, tradeID });
        commit('setState', { name: 'defaultProducts', value: data.stock });
      } catch (e) {
        console.log(e);
      }
    },

    async sendShortLoan({ commit, state, getters }) {
      try {
        const {
          accessID,
          tradeID,
          orderID,
          hostname,
          session,
          items,
          firstPayment,
        } = state;

        const {
          firstName,
          lastName,
          middleName,
        } = getters.FIO;

        const request = {
          accessID,
          hostname,
          session,
          orderID,
          comment: addComment(state),
          firstName,
          lastName,
          surName: middleName,
          email: state.form.clientEmail,
          firstPayment,
          goodParams: items,
          mobilePhone: state.form.clientMobilePhone,
          period: state.product.duration,
          tradeID,
          type: state.products.creditType,
        };

        const { data } = await posApi.sendRequest(request);
        commit('setState', {
          name: 'status',
          value: data,
        });
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
      }
    },
  },
  plugins: [
    createPersistedState({
      key: 'online_form_vuex',
      paths: [
        'accessID',
        'tradeID',
        'displayProducts',
        'form',
        'formType',
        'firstPayment',
        'totalSum',
        'product',
        'additionalServices',
        'selectedServices',
        'modals',
        'completeAddress',
        'items',
      ],
    }),
  ],
});
