/**
 * @param {string} text - ghbdtn
 * @returns {string} - –ø—Ä–∏–≤–µ—Ç
 */
export const switchLanguage = (text = '') => {
  const map = {
    q: '–π',
    w: '—Ü',
    e: '—É',
    r: '–∫',
    t: '–µ',
    y: '–Ω',
    u: '–≥',
    i: '—à',
    o: '—â',
    p: '–∑',
    '[': '—Ö',
    ']': '—ä',
    a: '—Ñ',
    s: '—ã',
    d: '–≤',
    f: '–∞',
    g: '–ø',
    h: '—Ä',
    j: '–æ',
    k: '–ª',
    l: '–¥',
    ';': '–∂',
    '\'': '—ç',
    z: '—è',
    x: '—á',
    c: '—Å',
    v: '–º',
    b: '–∏',
    n: '—Ç',
    m: '—å',
    Q: '–ô',
    W: '–¶',
    E: '–£',
    R: '–ö',
    T: '–ï',
    Y: '–ù',
    U: '–ì',
    I: '–®',
    O: '–©',
    P: '–ó',
    '{': '–•',
    '}': '–™',
    A: '–§',
    S: '–´',
    D: '–í',
    F: '–ê',
    G: '–ü',
    H: '–†',
    J: '–û',
    K: '–õ',
    L: '–î',
    ':': '–ñ',
    '"': '–≠',
    Z: '?',
    X: '—á',
    C: '–°',
    V: '–ú',
    B: '–ò',
    N: '–¢',
    M: '–¨',
    '<': '–ë',
    '>': '–Æ',
    '?': ',',
    '/': '.',
    ',': '–±',
    '.': '—é',
  };
  let value = '';
  for (let i = 0; i < text.length; i += 1) {
    if (map[text[i]]) {
      value += map[text[i]];
    } else value += text[i];
  }
  text = value;
  return text;
};
