/**
 * –í —ç—Ç–æ–º —Ñ–∞–π–ª–µ —Å–æ–±—Ä–∞–Ω—ã —Ñ—É–Ω–∫—Ü–∏–∏
 * –∫–æ—Ç–æ—Ä—ã–µ –ø—Ä–æ–≤–µ—Ä—è—é—Ç –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç—å —Å—Ç—Ä–∞—Ö–æ–≤–æ–∫ –∏ –∫–æ—Ä–æ–±–æ—á–Ω—ã—Ö —É—Å–ª—É–≥
 * @param {number} age - –≤–æ–∑—Ä–∞—Å—Ç –∫–ª–∏–µ–Ω—Ç–∞
 * @param {string} sex - –ø–æ–ª –∫–ª–∏–µ–Ω—Ç–∞
 * @param {boolean} isPensioner - —è–≤–ª—è–µ—Ç—Å—è –ª–∏ –∫–ª–∏–µ–Ω—Ç –ø–µ–Ω—Å–∏–æ–Ω–µ—Ä–æ–º
 * @returns {boolean}
 */

const MIN_AGE = 18;

/* –ü—Ä–æ–≤–µ—Ä–∫–∞ –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç–∏ —Å—Ç—Ä–∞—Ö–æ–≤–∫–∏ –∂–∏–∑–Ω–∏ */
const availableLifeInsurance = (age) => {
  const MAX_AGE = 69;
  return age >= MIN_AGE && age <= MAX_AGE;
};

/* –ü—Ä–æ–≤–µ—Ä–∫–∞ –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç–∏ —Å—Ç—Ä–∞—Ö–æ–≤–∫–∏ —Ä–∞–±–æ—Ç—ã */
const availableWorkInsurance = (age, sex, isPensioner) => {
  if (isPensioner) {
    return false;
  }
  const MAX_MALE_AGE = 60;
  const MAX_FEMALE_AGE = 55;
  const MALE = 'm';
  const MAX_AGE = sex === MALE ? MAX_MALE_AGE : MAX_FEMALE_AGE;

  return age >= MIN_AGE && age <= MAX_AGE;
};

/* –ü—Ä–æ–≤–µ—Ä–∫–∞ –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç–∏ –∫–æ—Ä–æ–±–æ—á–Ω—ã—Ö —É—Å–ª—É–≥ */
const availableBoxInsurance = age => age >= MIN_AGE;

export default {
  availableLifeInsurance,
  availableWorkInsurance,
  availableBoxInsurance,
};
