import store from '@/store';

import { queryParams } from './url';
import { isEmpty } from './functions';

export const requestParams = () => {
  const params = queryParams();

  if (!isEmpty(params)) {
    const items = [];
    const paramsList = Object.entries(params);

    // –§–æ—Ä–º–∏—Ä—É–µ–º —Å–ø–∏—Å–æ–∫ —Ç–æ–≤–∞—Ä–æ–≤ –∏–∑ –ø–æ–ª—É—á–µ–Ω–Ω—ã—Ö get –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤.
    paramsList.forEach(([key, value]) => {
      if (key.includes('obj')) {
        const [string, field] = key.split('_');
        const index = string.replace('obj', '');
        if (!items[index]) items[index] = {};

        // –¥–æ–±–∞–≤–ª—è–µ–º —Ç–æ–≤–∞—Ä—ã –≤ items
        items[index][field] = params[key];

        // —Å—á–∏—Ç–∞–µ–º —Å—É–º–º—É –≤—Å–µ—Ö —Ç–æ–≤–∞—Ä–æ–≤
        const totalSum = items.reduce((sum, item) => sum + item.price * item.count, 0);

        // –∑–∞–ø–∏—Å—ã–≤–∞–µ–º —Å—É–º–º—É —Ç–æ–≤–∞—Ä–æ–≤ –≤ store
        if (totalSum) store.commit('setState', { name: 'totalSum', value: totalSum });
        store.commit('setState', { name: 'items', value: items });
      } else {
        store.commit('setState', { name: key, value });
      }
    });
  }
};
