/**
 * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –¥–∞—Ç—É –≤ ISO-8601 —Ñ–æ—Ä–º–∞—Ç–µ
 * @param {string} date
 * @returns {string}
 * @example 10.11.1993 -> 1993-11-10
 */
export const formatDate = (date) => {
  const [day, month, year] = date.split('.');
  return `${year}-${month}-${day}`;
};
/**
 * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø–æ–ª–Ω—ã—Ö –ª–µ—Ç
 * @param {string} date - 14.05.1993
 * @returns {number} - 26
 */
export const getFullAge = (date = '') => {
  if (!date) return 0;

  const ONE_YEAR_IN_MILLISECONDS = 31556952000;
  const formattedDate = formatDate(date);

  return Math.floor((new Date().getTime()
    - new Date(formattedDate).getTime())
    / ONE_YEAR_IN_MILLISECONDS);
};
